.container{    
    margin-left: 4.8rem;
    margin-right: 4.8rem;
    display: flex;
    flex-direction: column;
    align-items: left;
    justify-content: center;
    max-width: 70rem;
    width: 100%;
    height: 100%;
    background-color: #f5f5f5;
    padding: 0 20px;
}
.container>*{
    padding: 0 40px;
}
.instructor{
    display: flex;
    align-items: center;
    flex-direction: row;
    width: 100%;
    height: auto;
    text-align: left;
    letter-spacing: normal;
    white-space: normal;
    list-style: none;
    padding:10px;
}

.instructorRate{
    list-style: none;
    font-size: 16px;
}
.instructorRate>*{
    padding: 5px 0;
}